* {
    margin: 0;
    padding: 0;
    font-size: 0;
}

.baseUrl {
    width: 500px;
    height: 20px;
    margin: 3px;
    font-size: 15px;
}

.txt {
    display: inline;
    font-size: 15px;
    cursor: default;
    -ms-user-select: none; 
    -moz-user-select: -moz-none; 
    -webkit-user-select: none; 
    -khtml-user-select: none; 
    user-select:none;
}

.topBar {
    display: flex;
    align-items: center;
    justify-content: center;
    position: sticky; 
    top: 0px;
    background: #eeeeee;
    z-index: 20;
    text-align: center;
}

.add {
    width:80px;
    height:30px;
    background:#74a7ff;
    border:none;
    margin:3px;
    font-size: 15px;
    font-weight: bold;
    color: white;
    cursor: default;
    -ms-user-select: none; 
    -moz-user-select: -moz-none; 
    -webkit-user-select: none; 
    -khtml-user-select: none; 
    user-select:none;
}

.download {
    width:80px;
    height:30px;
    background:#ff8874;
    border:none;
    margin:3px;
    font-size: 15px;
    font-weight: bold;
    color: white;
    cursor: default;
    -ms-user-select: none; 
    -moz-user-select: -moz-none; 
    -webkit-user-select: none; 
    -khtml-user-select: none; 
    user-select:none;
}