body {
    /* font-family: "Noto Sans KR", sans-serif !important; */
    line-height: 1.2;
}

.container {
    position: relative;
}

.frame {
    position: relative;
    width: 354px !important;
    height: 654px !important;
    border: 3px solid #d6d6d6;
}

.btn {
    position: absolute;
    width: 50px;
    height: 25px;
    background: #ffffff;
    visibility: hidden;
    z-index: 10;
    font-size: 15px;
}
/*
.frame:hover > :not(.show_detail) {
    opacity: 0.5;
}
*/
.container:hover > .btn{
    visibility: visible;
} 

.show_detail {
    position: absolute;
    top: 90px;
    width: 360px;
    height: 360px;
    font-size: 30px;
    text-align: center;
    font-weight: bold;
    padding-top: 30px;
    border-radius: 20px;
    color: rgb(51, 51, 51);
    visibility: hidden;
    z-index: 10;
    cursor: default;
    -ms-user-select: none; 
    -moz-user-select: -moz-none; 
    -webkit-user-select: none; 
    -khtml-user-select: none; 
    user-select:none;
}
.imgbox {
    object-fit: cover;
    width: 342px;
    height: 342px;
    margin: 5px;
    border: 1px solid #aaaaaa8e;
}
.option {
    display: inline-block;
    padding: 0;
    margin: 0;
    margin-left: 5px;
    margin-top: -5px;
    font-size: 40px;
    color: #ff6f6f;
    vertical-align: top;
    letter-spacing: -3px;
    padding-right: 5px;
    border: 3px solid #ff6f6f;
}
.order {
    display: inline-block;
    padding: 3px;
    margin: 0;
    margin-top: -5px;
    font-size: 40px;
    color: white;
    vertical-align: top;
    background-color: #ff6f6f;
}
.item_nm {
    display: block;
    margin: 0;
    padding: 0;
    font-size: 25px;
    margin-top: 15px;
    letter-spacing: -2px;
    margin-bottom: 15px;
    margin-left: 10px;
    color: #292929;
}
.item_option {
    display: block;
    margin-left: 10px;
    margin-bottom: 5px;
}
.item_option_type {
    display: inline-block;
    color: #ff6f6f;
    width: 65px;
    margin: 0;
    padding: 0;
    font-size: 16px;
}
.item_option_val {
    display: inline-block;
    color: #5a5a5a;
    width: 270px;
    margin: 0px;
    padding: 0px;
    white-space: normal;
    vertical-align: top;
    font-size: 16px;
}
.price_container {
    position: absolute;
    left: 15px;
    bottom: 40px;
    display: block;
    margin: 0;
    padding: 0;
    text-align: center;
}
.old_price {
    display: inline-block;
    margin: 0;
    padding: 0;
    vertical-align: middle;
    color: #5a5a5a;
    text-decoration: line-through;
    font-size: 20px;
}
.to {
    display: inline-block;
    margin: 0;
    padding: 0;
    margin-left: 10px;
    margin-right: 10px;
    vertical-align: middle;
    font-size: 30px;
}
.price {
    display: inline-block;
    margin: 0;
    padding: 3px;
    width: 170px;
    vertical-align: middle;
    padding-left: 5px;
    padding-right: 5px;
    font-size: 32px;
    color: #17aa2b;
    border: 3px solid #17aa2b;
}